// FAQ CSS

.common-FAQ{
    @include mobile{
        margin-bottom: 24px;
    }
}

.FAQ-description{
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 24px;
}

.common-FAQ-item{
    margin-bottom: 32px;
    &:nth-child(5){
        margin-bottom: 0;
    }
}

.common-FAQ-title{
    font-size: 20px;
    font-weight: bold;
    text-align: left;
}

.common-FAQ-text{
    font-size: 16px;
}